/* 전역 리셋 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background: linear-gradient(180deg, #e8f4ff 0%, #f5fbff 100%);
  color: #262626;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-container {
  width: 100%;
  max-width: 400px;
  padding: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-frame {
  width: 100%;
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 18px 45px rgba(97, 162, 211, 0.35);
  background: rgba(255, 255, 255, 0.9);
  padding: 16px;
  backdrop-filter: blur(12px);
  border: 1px solid rgba(144, 202, 249, 0.4);
}

.love-story-image {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 16px;
  object-fit: cover;
  user-select: none;
  -webkit-user-drag: none;
}

@media (max-width: 480px) {
  .app-container {
    padding: 16px;
  }

  .image-frame {
    padding: 12px;
    border-radius: 20px;
  }

  .love-story-image {
    border-radius: 12px;
  }
}
